<template>
    <v-card flat>
        <br>
        <v-row class="text-right">
            <v-col cols="12" class="mb-n2 text-rigt">
        <router-link class="mr-2" to="/addstore">
            <v-icon color="#E47911" size="16px" left> mdi-pencil </v-icon> Add Store 
        </router-link>
            </v-col>
        </v-row>
        <v-data-table
        :headers="headers"
        :items="items"
        :loading="loading"
        loading-text="Loading... Please wait"
        :items-per-page="5"
        item-key="name"
        class="elevation-1">
        <template v-slot:item.store="{ item }">
            {{ item.store.name  }}  |  
            <router-link
            :to="getLink(item.store.unique_id)"
            >
            View Store <v-icon class="ml-1" color="#E47911" size="16px"> mdi-eye </v-icon>
            </router-link>
        </template>
        <template v-slot:item.status="{ item }">
            <v-chip
            :color="getColor(item.store.status)"
            dark>
            {{ item.store.status | name_status }}
            </v-chip>
        </template>
        </v-data-table>
    </v-card>
</template>


<script>
import StoreService from '@/services/StoreService';
import Swal from 'sweetalert2'

export default {
    name: 'StoresOwned',

    data: () => ({
        headers: [
          {
            text: "Store",
            align: 'start',
            value: 'store',
          },
          { text: 'Status',
            value: 'status',
          },
        ],
        items: [ ],
        loading: true,
    }),

    filters:{
      name_status(value){
        switch (value) {
          case 'A': return 'Active'
            break;
          
          case 'ED': return 'Everyday'
            break;

          case 'S': return 'Suspended'
            break;
          
          case 'V': return 'Vacation'
            break;

          case 'DA': return 'Denied activation'
            break;
        
          default: return 'Pending activation'
            break;
        }
      }
    },
    
    computed: {
    idUser(){
      return this.$store.state.auth.user.unique_id
    }
  },

  methods: {
    getLink (unique_id) {
      return '/viewstore/'+unique_id;
    },

  getColor (status) {
        if (status == "A") return 'green'
        else if (status == 'ED') return 'purple'
        else if (status == 'S') return 'red'
        else if (status == 'DA') return 'gray'
        else if (status == 'V') return 'yellow'
        else return 'orange'
      },
    
    refresh_get(){
      StoreService
        .stores_owned(this.idUser)
        .then(res => {
          this.items = res;
          this.loading = false;
        })
        .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
        });
        this.loading = false,
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })
    },
  },
  
  mounted() {
    this.refresh_get();
  }
    
}
</script>