<template>
    <v-card flat>
        <br>
        <v-row class="text-right">
            <v-col cols="12" class="mb-n2 text-rigt">
            <router-link class="mr-2" to="/addreferral">
                <v-icon color="#E47911" size="16px" left> mdi-pencil </v-icon> Add Refer 
            </router-link>
            </v-col>
        </v-row>

    <v-data-table
    :headers="headers"
    :items="referrals"
    :loading="loading"
    loading-text="Loading... Please wait"
    hide-default-footer
    :page.sync="page"
    :items-per-page="itemsPerPage"
    @page-count="pageCount = $event"
    >
    <template v-slot:item.unique_id="{ item }">
        <router-link :to="{ name: 'ViewReferral', params: { 'id': item.unique_id }}"> View referral </router-link>
    </template>
    </v-data-table>
    </v-card>

</template>

<script>
  import ReferralService from '@/services/ReferralService';
  import Swal from 'sweetalert2'

export default {
    data: () => ({
      loading: false,
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      referrals: [

      ],
      headers: [
        {
          text: 'Referral',
          value: 'name'
        },
        {
          text: 'Date',
          value: 'registered'
        },
        { 
          text: 'Referred to',
          value: 'by'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
        },
      ],
    }),

    computed: {
    idUser(){
        if(this.$router.params)
            return this.$router.params.id

        return this.$store.state.auth.user.unique_id
    }
  },

    methods: {
        list_user_referrals(){
            this.loading = true;
            ReferralService
            .list_user_referrals(this.idUser)
            .then(res => {
              console.log(res)
              this.referrals = res;
              this.loading = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
    },

    mounted(){
        this.list_user_referrals();
    }
    
}
</script>